#overlay {
  background-color: rgba($color: #000000, $alpha: 0.3);
  position: fixed;
  left: 0rem;
  top: 40px;
  visibility: hidden;
  transition: 0.3s;
  width: 100%;
  height: 100%;
  z-index: 10;
}

